import React from 'react';
import PropTypes from 'prop-types';
import { ThemeToggler } from 'gatsby-plugin-dark-mode';

// Constants for theme identifiers
const DARK_THEME = 'dark-mode';
const LIGHT_THEME = 'light-mode';

export class BThemeToggler extends React.Component {
  render() {
    return (
      <ThemeToggler>
        {({ theme, toggleTheme }) => (
          <div className="form-check form-switch">
            <input
              className="form-check-input"
              type="checkbox"
              id="darkModeSwitch"
              onChange={e => {
                // Toggle between dark-mode and light-mode based on checkbox state
                const newTheme = e.target.checked ? DARK_THEME : LIGHT_THEME;
                toggleTheme(newTheme);
              }}
              checked={theme === DARK_THEME}
            />
            <label className="form-check-label" htmlFor="darkModeSwitch">
              <small className="xsmall">Dark Mode</small>
            </label>
          </div>
        )}
      </ThemeToggler>
    );
  }
}

BThemeToggler.propTypes = {};
